import type { Direction } from '../config';

export const calculatePointfromEvent = (
  event: MouseEvent | TouchEvent,
  offsetElement?: HTMLElement,
  direction: Direction = 'left-to-right',
): number => {
  const x =
    event instanceof MouseEvent ? event.clientX : event.touches[0].clientX;
  const y =
    event instanceof MouseEvent ? event.clientY : event.touches[0].clientY;

  let point = 0;
  let offset = 0;

  if (direction === 'left-to-right') {
    point = x;
    offset = offsetElement?.getBoundingClientRect().left ?? 0;
  } else if (direction === 'right-to-left') {
    point = x;
    offset = offsetElement?.getBoundingClientRect().right ?? 0;
  } else if (direction === 'top-to-bottom') {
    point = y;
    offset = offsetElement?.getBoundingClientRect().top ?? 0;
  } else if (direction === 'bottom-to-top') {
    point = y;
    offset = offsetElement?.getBoundingClientRect().bottom ?? 0;
  }
  return point - offset;
};
